.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 1360px;
    height: 40px;
    margin: 0 40px 0;
    padding-top: 32px;
    }
    
    .header__logo {
    display: flex;
    flex-direction: row;
    min-width: 273px;
    height: 40px;
    align-items: center;
    gap: 24px;
    }
    
    .header__logo-image{
    width: 89px;
    height: 40px;
    }

    .header__date {
    min-width: 160px;
    height: 24px;
    font-family: "Cabinet Grotesk";
    font-size: 1.25rem;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 0em;
    }
    
    .header__avatar-bar {
    max-width: 345px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    gap: 12px;
    }

    .header__add-button {
    min-width: 116px;
    height: 24px;
    font-family: "Cabinet Grotesk";
    font-size: 1.25rem;
    font-weight: 700;
    line-height: 24px;
    border: none;
    padding: 0;
    }  
    
    .header__user-name {
    max-width: 157px;
    height: 25px;
    font-family: "Cabinet Grotesk";
    font-size: 1.25rem;
    font-weight: 700;
    line-height: 25px;
    letter-spacing: 0em;
    }
    
    .header__avatar {
    max-width:40px;
    height:40px;
    border-radius: 50%;
    object-fit: cover;
    border: 1px solid rgba(0, 0, 0, 0.50);
    }

    @media screen and (max-width: 780px) {
        .header {
            max-width: 780px;
            }
            
            .header__logo {
            min-width: 140px;
            height: 32px;
            gap: 10px;
            }
            
            .header__logo-image{
            max-width: 60px;
            height: 24px;
            }
        
            .header__date {
            max-width: 70px;
            height: 20px;
            font-size: 0.7rem;
            }
            
            .header__avatar-bar {
            max-width: 248px;
            gap: 8px;
            }
        
            .header__add-button {
            min-width: 90px;
            height: 24px;    
            font-size: 0.7rem;
            }  
            
            .header__user-name {
            max-width: 100px;
            height: 25px;
            font-weight: 0;
            font-size: 0.7rem;
            }
            
            .header__avatar {
            max-width:32px;
            height:32px;
            }
    
    
    }

